import { Component, OnInit, Input } from '@angular/core';
import { Chart } from 'node_modules/chart.js';
import { Question } from 'src/app/models/question.model';

@Component({
  selector: 'app-graficas-usuario',
  templateUrl: './graficas-usuario.component.html',
  styleUrls: ['./graficas-usuario.component.scss']
})
export class GraficasUsuarioComponent implements OnInit {
  @Input()
  question: Question;

  constructor() { }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void{
    this.drawChart();
  }

  drawChart(){
    var optionTitles = [];
    var optionVotes = [];
    this.question.options.forEach(function (option) {
      optionTitles.push(option.title);
      optionVotes.push(option.votes);
    });

    var ctx = document.getElementById("question-" + this.question._id);
    var myChart = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: optionTitles,
        datasets: [{
          label: '# of Votes',
          data: optionVotes,
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(255, 159, 64, 0.2)'
          ],
          borderColor: [
            'rgba(255, 99, 132, 1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)',
            'rgba(255, 159, 64, 1)'
          ],
          borderWidth: 1
        }]
      },
      options: {
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            }
          }]
        }
      }
    });
  }
}